import { defHttp } from '/@/utils/http/axios';
import type { SelectProps } from 'ant-design-vue';
import { ref } from 'vue';

enum Api {
  //顶端指标
  getTopIndexUrl = '/da/comprehensive-page/index',
  // 人员持证
  getPersonCertUrl = '/da/comprehensive-page/person/cert',

  //车辆统计
  getVehicleUrl = '/da/comprehensive-page/vehicle',
  // 考核评价
  getEvalResultUrl = '/da/comprehensive-page/eval/result',

  //合同统计
  getContractUrl = '/da/comprehensive-page/contract',
  //任务统计
  getTaskUrl = '/da/comprehensive-page/scene/task',
  //培训统计
  getTrainingtUrl = '/da/comprehensive-page/training',
}
export const getTopIndexData = (params) => defHttp.post({ url: Api.getTopIndexUrl, params }, {});
export const getPersonCertData = (params) =>
  defHttp.post({ url: Api.getPersonCertUrl, params }, {});

export const getVehicleData = (params) => defHttp.post({ url: Api.getVehicleUrl, params }, {});
export const getEvalResultData = (params) =>
  defHttp.post({ url: Api.getEvalResultUrl, params }, {});

export const getTaskResultData = (params) => defHttp.post({ url: Api.getTaskUrl, params }, {});

export const getContractData = (params) => defHttp.post({ url: Api.getContractUrl, params }, {});
export const getTrainingtData = (params) => defHttp.post({ url: Api.getTrainingtUrl, params }, {});

